export default {
	data() {
		return {
			bHasGetCode: false, //第一次获取验证码
			bHasPhoneNumber: false,
			bWaitCode: false,
			bHasCode: false,
			iPhoneNumber: '',
			iCode: '',
			iCountTime: 6,
			bSuccessLogin: false //成功登录
		};
	},
	computed: {
	},
	watch: {
	},
	methods: {
		fnGetCode() {
			if (!this.iPhoneNumber) {
				return
			}
			this.iCountTime = 6;
			this.bWaitCode = true;
			this.bHasGetCode = true;
			this.bHasCode = false;
			console.log(this.iCountTime)
			let that = this;
			let timer = setInterval(function(){
				if (that.iCountTime > 0) {
					that.iCountTime --
				} else {
					that.bWaitCode = false;
					that.bHasCode = true;
					clearInterval(timer)
				}
			}, 1000)
			
				
			if(this.bWaitCode) {
				
			}
		},
		fnBegin() {
			uni.navigateTo({
			    url: '../video/video'
			});
		}
	}
};
